.App {
  text-align: center;
  display: flex;
  /* justify-content: center; */
  align-items: center; 
  flex-direction: column;
  height: 100vh;
}

#nav{
  background-color: rgb(0, 161, 0);
  width: 100vw;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.cards{
  display: flex;
}
.card{
  margin: 10px;
  width: 350px;
  height: 370px;
}

#buyButton{
  background-color: rgb(0, 161, 0);
}

.cardBuy{
  width: 750px;
  height: 500px;
  display: flex;
  margin-top: -40px;
  padding-top: 0;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  border-radius: 5px;
  /* margin-bottom: 20px; */
}

.gridCard{
  width: 750px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 40px;
  font-weight: 500;
  border-radius: 5px 5px 0px 0px;
  background-color: rgba(224, 224, 224, 0.555);

}

.cardItem{
  width: 750px;
  display: flex;
  justify-content: space-around;

}
.cardItem img{
  width: 80px;
  height: 78px;
}

.item{
  width: 187px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: 1px solid rgba(192, 192, 192, 0.5);
}

.imageName{
  display: flex;
  justify-content: space-around;
  align-items: center;
  font-weight: 500;
}

.items{
  width: 750px;
  height: 100px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.items h1{
  font-weight: normal!important;
}

.items p{
  /* margin-top: -30; */
  margin-bottom: 0;
  margin-left: 10px;
}

.bag{
  width: 750px;
  height: 100px;
  display: flex;
  align-items: center;

}

.continue{
  text-decoration: underline;
  width: 250px;
  cursor: pointer;
}

#cart{
  margin-left: -5px;
  margin-right: 5px;
  width: 18px;
}

#heading{
  font-size: 40px;
  color: #414141;
}

.modalCard{
  display: flex;
  justify-content: center;
  align-items: center;
}

.modalInfos{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}


@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
